// 场景总文件
// 引入Three.js
import * as THREE from "three";
import gui from "../Three/gui";
/**
 * 创建场景对象Scene
 */
const scene = new THREE.Scene();

/**
 * 光源设置
 */
// 平行光1
const directionalLight = new THREE.DirectionalLight(0xffffff, 0.9);
directionalLight.position.set(400, 200, 300);
scene.add(directionalLight);
// 平行光2
const directionalLight2 = new THREE.DirectionalLight(0xffffff, 1.9);
directionalLight2.position.set(-400, -200, -300);
scene.add(directionalLight2);
//环境光
const ambient = new THREE.AmbientLight(0xffffff, 0.9);
scene.add(ambient);


const dirHelper = new THREE.DirectionalLightHelper(directionalLight, 5);
const dirFolder = gui.addFolder("灯光");
// gui控制环境光光照强度`.intensity`
dirFolder.add(ambient, "intensity", 0, 2).name("环境光.intensity");
dirFolder.add(directionalLight, "intensity", 0, 2);
dirFolder
  .add(directionalLight.position, "y", 0, 300)
  .onChange(function (value) {
    dirHelper.update();
  });
const obj = {
  R: 100,
  angle: 0,
};
dirFolder.add(obj, "R", 0, 300).onChange(function (value) {
  directionalLight.position.x = value * Math.cos(obj.angle);
  directionalLight.position.z = value * Math.sin(obj.angle);
  dirHelper.update();
});
dirFolder.add(obj, "angle", 0, Math.PI * 2).onChange(function (value) {
  directionalLight.position.x = obj.R * Math.cos(value);
  directionalLight.position.z = obj.R * Math.sin(value);
  dirHelper.update();
});
// //辅助观察的坐标系
// const axesHelper = new THREE.AxesHelper(100);
// scene.add(axesHelper);

// 场景天空盒
const textureCubeLoader = new THREE.CubeTextureLoader().setPath("./textures/");
const textureCube = textureCubeLoader.load([
  "px.jpg",
  "nx.jpg",
  "py.jpg",
  "ny.jpg",
  "pz.jpg",
  "nz.jpg",
]);
scene.background = textureCube;
scene.environment = textureCube;

//设置纹理贴图编码方式和WebGL渲染器或composer后期伽马修正一致
// material.map.colorSpace = THREE.sRGBEncoding;

// 设置雾化效果，雾的颜色和背景颜色相近，这样远处网格线和背景颜色融为一体
scene.fog = new THREE.Fog(0xffffff, -100, 2000);

export { scene };
