<template>
  <div class="scene" ref="webgl"></div>
  <div id="bu" class="bu">播放</div>
</template>
  <script>
// 引入Three.js
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { scene } from "../components/Three/scene";
import { camera, controls } from "../components/Three/camera";
import renderer from "../components/Three/renderer";
import { model, arr } from "../components/model/company";
import { raychoose } from "../components/Three/raychoose";

import { EffectComposer } from "three/examples/jsm/postprocessing/EffectComposer.js";
import { RenderPass } from "three/examples/jsm/postprocessing/RenderPass.js";
import { ShaderPass } from "three/examples/jsm/postprocessing/ShaderPass.js";
// import { CopyShader } from 'three/examples/jsm/postprocessing/CopyShader.js';
import { OutlinePass } from "three/examples/jsm/postprocessing/OutlinePass.js";
// 伽马校正
import { GammaCorrectionShader } from "three/examples/jsm/shaders/GammaCorrectionShader.js";
import { FXAAShader } from "three/examples/jsm/shaders/FXAAShader.js";
// import { composer,outlinePass} from '../components/Three/composer'
export default {
  name: "ThreeCanvas",
  components: {},
  data() {
    return {};
  },
  mounted() {
    model.position.y = -20;
    scene.add(model); //网格模型添加到场景中

    this.$refs.webgl.appendChild(renderer.domElement);

    // 创建后处理对象EffectComposer，WebGL渲染器作为参数
    const composer = new EffectComposer(renderer);
    // 创建一个渲染器通道，场景和相机作为参数
    const renderPass = new RenderPass(scene, camera);
    // 设置renderPass通道
    composer.addPass(renderPass);

    // 创建OutlinePass通道
    const v2 = new THREE.Vector2(window.innerWidth, window.innerWidth);
    const outlinePass = new OutlinePass(v2, scene, camera);
    // outlinePass.selectedObjects = [mesh];
    outlinePass.visibleEdgeColor.set(0x00ffff);
    outlinePass.edgeThickness = 4;
    outlinePass.edgeStrength = 6;
    composer.addPass(outlinePass);

    // 创建伽马校正通道
    const gammaPass = new ShaderPass(GammaCorrectionShader);
    composer.addPass(gammaPass);

    // FAXX抗锯齿通道
    const FXAAPass = new ShaderPass(FXAAShader);
    // `.getPixelRatio()`获取`renderer.setPixelRatio()`设置的值
    const pixelRatio = renderer.getPixelRatio(); //获取设备像素比
    // width、height是canva画布的宽高度
    FXAAPass.uniforms.resolution.value.x = 1 / (window.innerWidth * pixelRatio);
    FXAAPass.uniforms.resolution.value.y = 1 / (window.innerWidth * pixelRatio);
    composer.addPass(FXAAPass);

    renderer.domElement.addEventListener("click", function (event) {
        if(raychoose(event, arr, camera)){
            outlinePass.selectedObjects = [raychoose(event, arr, camera)];
        }

    });

    // 渲染循环
    function render() {
      //   renderer.render(scene, camera);
      composer.render(scene, camera); //执行渲染操作

      requestAnimationFrame(render);
    }
    render();

    window.onresize = function () {
      renderer.setSize(window.innerWidth, window.innerHeight);
      camera.aspect = window.innerWidth / window.innerHeight;
      camera.updateProjectionMatrix();
    };
  },
  methods: {
  },
};
</script>
  <style scoped>
.scene {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 10;
  left: 0;
  top: 0;
}
.bu {
  background: rgba(0, 0, 0, 0.6);
  width: 60px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  color: #fff;
  display: inline-block;
  border-radius: 30px;

  position: absolute;
  z-index: 999;
  left: 50%;
  margin-left: -30px;
  bottom: 100px;
}

.bu:hover {
  cursor: pointer;
}
</style>